import React, { useEffect, useState } from "react";
import icon from "./images/kolorav.png";
import "./styles/vscode.css";
import Topbar from "./Topbar";

function Vscode({ app }) {
  const [filetype, setfiletype] = useState("html-file");
  useEffect(() => {
    const filetypes = document.getElementsByClassName("vscode-btns");
    for (const files of filetypes) {
      files.style.backgroundColor = "transparent";
    }
    document.getElementById(filetype).style.backgroundColor =
      "hsl(230, 41%, 70%)";
  }, [filetype]);
  return (
    <div
      className="all_app"
      id="vscode_app"
      style={{
        justifyContent: "center",
        opacity: "1",
        overflow: "hidden",
      }}
    >
      <Topbar
        app_id={"vscode_app"}
        title={"Kolorav"}
        window_id={app}
        icon={icon}
      />
      <div className="file-bar">
        <button
          className="vscode-btns"
          id="html-file"
          onClick={() => setfiletype("html-file")}
        >
          index.html
        </button>
        <button
          className="vscode-btns"
          id="css"
          onClick={() => setfiletype("css")}
        >
          style.css
        </button>
        <button
          className="vscode-btns"
          id="js"
          onClick={() => setfiletype("js")}
        >
          index.js
        </button>
        <button
          className="vscode-btns"
          id="output"
          onClick={() => setfiletype("output")}
        >
          Output
        </button>
      </div>
      <textarea className="code"></textarea>
    </div>
  );
}

export default Vscode;
